import React,{Component} from 'react';
import { Server } from "net";
import { strict } from 'assert';
import { stringify } from 'querystring';
import './Box1.css';
 

class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
            selectValue : 0 , 
            selectDate : "",
            newday: "",
            newdate : "",
            newmonth:"",
            newyear:"",
            arrmonth : ['January','February','March','April','May','June','July','August','September','October','November','December'],
            arrdays : ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'],
            newdatestring:"",
            submitted:false,
            f_day :"",
            f_date :"",
            f_month : "",
            f_year:"",
            m_day :"",
            m_date :"",
            m_month : "",
            m_year:"",
            n_day :"",
            n_date :"",
            n_month : "",
            n_year:"",
            p_day:"",
            p_date:"",
            p_month:"",
            p_year:"",
            hoveryes:false,


            selectedOption1 : "",
            selectedOption2 : "",
            selectedOption3 : "",

        };
        this.handleDropdownChange = this.handleDropdownChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handlec0 = this.handlec0.bind(this);
        this.handlec1 = this.handlec1.bind(this);
        this.handleOptionChange1 = this.handleOptionChange1.bind(this);
        this.handleOptionChange2 = this.handleOptionChange2.bind(this);
        this.handleOptionChange3 = this.handleOptionChange3.bind(this);
    }

    handleDropdownChange(e) {
        this.setState({ selectValue: e.target.value });
      }
    handleClick = (p) => {
        var temp = new Date((this.state.selectDate));
        var interval = Number(this.state.selectValue);
        var fertility_temp = new Date((this.state.selectDate));
        var more_fertility = new Date((this.state.selectDate));
        var next_date = new Date((this.state.selectDate));
        var preg_date = new Date((this.state.selectDate));

        temp.setDate((temp.getDate()+interval)-15);
        fertility_temp.setDate((fertility_temp.getDate()+interval)-18);
        more_fertility.setDate((more_fertility.getDate()+interval)-13);
        next_date.setDate((next_date.getDate()+interval)-14);
        preg_date.setDate((preg_date.getDate()+interval)-1);


        this.setState({newdate: temp.getDate(),newmonth:temp.getMonth(),newyear:temp.getFullYear(),newday:temp.getDay()});
        this.setState({f_day:fertility_temp.getDay(),f_date : fertility_temp.getDate(),f_month:fertility_temp.getMonth(),f_year : fertility_temp.getFullYear()})
        this.setState({m_day:more_fertility.getDay(),m_date : more_fertility.getDate(),m_month:more_fertility.getMonth(),m_year : more_fertility.getFullYear()})
        this.setState({n_day:next_date.getDay(),n_date : next_date.getDate(),n_month:next_date.getMonth(),n_year : next_date.getFullYear()})
        this.setState({p_day:preg_date.getDate(),p_date:preg_date.getDate(),p_month:preg_date.getMonth(),p_year:preg_date.getFullYear()})
        
      //  console.log(temp + "hello"+ fertility_temp +"hello" + more_fertility);

        var month = temp.getMonth()+1;
        var date = temp.getDate();
        if(month<10){
          month = "0"+ month;
        }
        if(date<10){
          date = "0"+ date;
        }

        let temp_str =  temp.getFullYear()+"-"+month+"-"+date;
        console.log(temp_str)
        this.setState({newdatestring:temp_str,submitted:true});


        
    }
    handlec1(){
      this.setState({hoveryes:true})
    }
    handlec0(){
      this.setState({hoveryes:false})
    }
    handleOptionChange1(e){
      this.setState({selectedOption1:e.target.value})
    }
    handleOptionChange2(e){
      this.setState({selectedOption2:e.target.value})
    }
    handleOptionChange3(e){
      this.setState({selectedOption3:e.target.value})
    }

    

    render(){
        return(
            <div className ="mainbox">
              <div className="container">
                  <div className="row" style={{paddingTop:"5%"}}>
                    <div className="col">
                      <p  style={{marginLeft:"15%" ,color:"#163948" ,paddingLeft:"2%"}}><strong>When did you get your most recent period?</strong> </p>
                    </div>
                    <div className="col">
                      <input className="inputDate"  type="date" onChange={(event) => this.setState({selectDate: event.target.value})}/>
                    </div>
                  </div>
                  <div className="row" style={{paddingTop:"2%"}}>
                    <div className="col">
                      <p  style={{marginLeft:"15%" ,color:"#163948" ,paddingLeft:"2%", display:"inline"}}><strong>What is your average menstrual cycle length</strong> </p>
                      <a style={{color:"blue" ,display:"inline"}} onMouseOver={this.handlec1} onMouseLeave={this.handlec0}><strong>?</strong></a>
                      {this.state.hoveryes?<span className="tooltiptext"><p style={{padding:"5%"}}>Your menstrual cycle length is the number of days from the day your period starts to the day before your next period starts</p></span>:null}
                    </div>
                    <div className="col">
                    <select style={{marginLeft:"15%", width : "200px", height:"50px" ,color:"#163948" ,borderRadius:"25px",paddingLeft:"4%",borderWidth:"3px",borderColor:"#163948"}} id ="dropdown" onChange={this.handleDropdownChange}>
                                    <option value="">Select days</option>
                                    <option value="20">20 days</option>
                                    <option value="21">21 days</option>
                                    <option value="22">22 days</option>
                                    <option value="23">23 days</option>
                                    <option value="24">24 days</option>
                                    <option value="25">25 days</option>
                                    <option value="26">26 days</option>
                                    <option value="27">27 days</option>
                                    <option value="28">28 days</option>
                                    <option value="29">29 days</option>
                                    <option value="30">30 days</option>
                                    <option value="31">31 days</option>
                                    <option value="32">32 days</option>
                                    <option value="33">33 days</option>
                                    <option value="34">34 days</option>
                                    <option value="35">35 days</option>
                                    <option value="36">36 days</option>
                                    <option value="37">37 days</option>
                                    <option value="38">38 days</option>
                                    <option value="39">39 days</option>
                                    <option value="40">40 days</option>
                                    <option value="41">41 days</option>
                                    <option value="42">42 days</option>
                                    <option value="43">43 days</option>
                                    <option value="44">44 days</option>
                                    <option value="45">45 days</option>
                            </select>
                    </div>
                  </div>
                  <div className="row" style={{paddingTop:"1.5%"}}>
                    <div className="col">
                      <p  style={{marginLeft:"15%" ,color:"#163948" ,paddingLeft:"2%"}}><strong>How is your period flow?</strong> </p>
                    </div>
                    <div className="col">
                    <form style={{marginLeft:"15%"}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption1 === "0"} 
                                            onChange={this.handleOptionChange1} />
                                Light
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption1 === "1"} 
                                onChange={this.handleOptionChange1} />
                            Medium
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption1 === "2"} 
                                onChange={this.handleOptionChange1} />
                                Heavy
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption1 === "3"} 
                                onChange={this.handleOptionChange1} />
                                Spotting
                            </label>
                        </div>
                            
                    </form>
                    </div>

                  </div>
                  <div className="row" style={{paddingTop:"1.5%"}}>
                      <div className="col">
                        <p  style={{marginLeft:"15%" ,color:"#163948" ,paddingLeft:"2%"}}><strong>Do you experience cramps?</strong> </p>
                      </div>
                      <div className="col">
                      <form style={{marginLeft:"15%"}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption2 === "0"} 
                                            onChange={this.handleOptionChange2} />
                                Yes, during the first few days of the cycle
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption2 === "1"} 
                                onChange={this.handleOptionChange2} />
                              Yes, during the entire period cycle
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption2 === "2"} 
                                onChange={this.handleOptionChange2} />
                                No
                            </label>
                        </div>
                            
                    </form>
                      </div> 
                  </div>
                  <div className="row" style={{paddingTop:"1.5%"}}>
                      <div className="col">
                        <p  style={{marginLeft:"15%" ,color:"#163948" ,paddingLeft:"2%"}}><strong>Do you experience any PMS symptoms?</strong> </p>
                      </div>
                      <div className="col">
                      <form style={{marginLeft:"15%"}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption3 === "0"} 
                                            onChange={this.handleOptionChange3} />
                                Moody
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption3 === "1"} 
                                onChange={this.handleOptionChange3} />
                              Under the weather
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption3 === "2"} 
                                onChange={this.handleOptionChange3} />
                                Headache
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption3 === "3"} 
                                onChange={this.handleOptionChange3} />
                                Fatigue
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="4" 
                                checked={this.state.selectedOption3 === "4"} 
                                onChange={this.handleOptionChange3} />
                                Breast Tenderness
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="5" 
                                checked={this.state.selectedOption3 === "5"} 
                                onChange={this.handleOptionChange3} />
                                Abdominal cramps 
                            </label>
                        </div>
                            
                    </form>
                      </div> 
                  </div>

                  <div className="row" style={{paddingTop:"1.5%"}}>
                        <button className="myButton" style={{marginLeft:"45%",marginTop:"2%"}} onClick ={()=> this.handleClick()}>Submit</button> 
                  </div>




                  <div >

                    {this.state.submitted?<div style={{backgroundColor:"white",marginTop:"5%",paddingBottom:"3%",border:"solid",borderRadius:"28px"}}>
                      <div className="row">
                            <div className="col">
                                <div className="row">
                                  <h5 style={{textAlign:"left",paddingTop:"5%",color:"#54595F",marginLeft:"10%"}}>Predicted ovulation date : </h5>
                                  <div className="row">
                                  <h5 style={{textAlign:"left",paddingTop:"0.5%",color:"black",marginLeft:"-200px",marginTop:"60px"}}>{this.state.arrdays[this.state.newday]}, {this.state.newdate} {this.state.arrmonth[this.state.newmonth]} {this.state.newyear}</h5>
                                  </div>
                                  

                                </div>
                                <div className="row">
                                  <h5 style={{textAlign:"left",paddingTop:"5%",color:"#54595F",marginLeft:"10%"}}>Most fertile days : </h5>
                                  <h5 style={{textAlign:"left",paddingTop:"0.5%",color:"black",marginLeft:"10%"}}>{this.state.arrdays[this.state.f_day]}, {this.state.f_date} {this.state.arrmonth[this.state.f_month]} {this.state.f_year} to {this.state.arrdays[this.state.newday]}, {this.state.newdate} {this.state.arrmonth[this.state.newmonth]} {this.state.newyear}</h5>
                                </div>
                                <div className="row">
                                  <h5 style={{textAlign:"left",paddingTop:"5%",color:"#54595F",marginLeft:"10%"}}>Potentially fertile days : </h5>
                                  <h5 style={{textAlign:"left",paddingTop:"0.5%",color:"black",marginLeft:"10%"}}>{this.state.arrdays[this.state.n_day]}, {this.state.n_date} {this.state.arrmonth[this.state.n_month]} {this.state.n_year} and {this.state.arrdays[this.state.m_day]}, {this.state.m_date} {this.state.arrmonth[this.state.m_month]} {this.state.m_year}</h5>
                                </div>
                                <div className="row">
                                  <h5 style={{textAlign:"left",paddingTop:"5%",color:"#54595F",marginLeft:"10%"}}>Take Pregnancy Test : </h5>
                                  </div>
                                <div className="row">
                                <h5 style={{textAlign:"left",paddingTop:"1%",color:"black",marginLeft:"10%"}}>{this.state.arrdays[this.state.p_day]} {this.state.p_date} {this.state.arrmonth[this.state.p_month]} {this.state.p_year}</h5>
                                
                                </div>
                                
                            </div>
                            <div className="col">
                                <p style={{marginLeft:"35%",marginTop:"25%"}}><strong>View Ovulation Date in Calendar</strong></p>
                                <input className="inputDate" style={{marginLeft:"40%",marginTop:"0%"}} type="date" value ={this.state.newdatestring} />
                              
                            </div>

                        </div>

                        <div>
                        <br />
                        <br />
                          <div className="row">
                              <a style={{marginLeft:"45%",marginTop:"2%"}} className="myButton" href="http://DivyanshiSingh7.github.io/tracker4/" >Redo</a>
                              <button className="myButton" style={{marginLeft:"70%",marginTop:"2%"}}>Stop Ovulation Tracker</button> 
                          </div>
                      </div>
                    </div>:null}

                  </div>





      





















              </div>

            </div>








            
        )
    }
} 

export default Box1

